import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '@/components/home'
import about from '@/components/about'
import document from '@/components/document'
import html from '@/view/html'
import css from '@/view/css'
import js from '@/view/js'
import node from '@/view/node'
import vuecli from '@/view/vuecli'
import xcx from '@/view/xcx'
import python from '@/view/python'
import php from '@/view/php'
import java from '@/view/java'
Vue.use(VueRouter)
let router = new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/',
      component: home
    },
    {
      path: '/document',
      component: document,
      children: [
        {
          path: '',
          component: html
        },
        {
          path: 'css',
          component: css
        },
        {
          path: 'js',
          component: js
        },
        {
          path: 'node',
          component: node
        },
        {
          path: 'vuecli',
          component: vuecli
        },
        {
          path: 'xcx',
          component: xcx
        },
        {
          path: 'python',
          component: python
        },
        {
          path: 'php',
          component: php
        },
        {
          path: 'java',
          component: java
        }
      ]
    },
    {
      path: '/about',
      component: about
    },
    {
      path: '*',
      redirect: '/home'
    }
  ]
})
export default router



