<template>
<div class="index">
  <div class="about-main index-content">
    <div class="about-main-right">
      <h2>记性越来越差了，都忘了做这个东西是干嘛的了！</h2>
      <p>说起记性差了，让我想到点东西，记录一下曾经写过的东西，也是见证一下自己的成长，以前做的一些项目遇到的一些bug，开始盲目的去猜测问题到后来的一步步的测试问题，也会遇到一些脑补的问题，比如书写的不规范导致的错误，让自己差点自闭，查阅各种资料的去解决，现在想想可能会很好笑的，有点二，对于自己而言都是要经历的，有的时候真的挺佩服自己的，有一天听到说python可以写智能然后自己很好奇就买了一本书叫什么我忘了，然后就开始学习pyhton，跟着书里面一步一步的去操作，但是也遇到了很多的问题，作为一个小白，只能一步一的去解决，最后写了一飞机大战的游戏，写完之后有点成就感，可是发现对于目前的自己来说完全用不到的，后来就放弃了学习，又去接触php，又去接触node，回过头发现自己遗忘了很多学过的东西，用网站做为笔记本，记录一下自己遇到一些有趣的的bug吧！</p>
      <h2>时间过得太快，有些遇到的问题可能停留在某个时间段了，有缘看到随手记录的朋友，愿能给你一个解决问题的提示吧！（没整理完呢，有时间慢慢整理吧,  谅解）</h2>
    </div>
  </div>
</div>
</template>
<script>
export default {
  name: 'home'
}

</script>
